<template>
  <v-container>
    <h1 class="my-2 d-flex justify-center">Etappenübersicht</h1>
    <Etappenziel v-for="ziel in etappenziele"
                 :key="ziel.id" :nr="ziel.nr" :zielbeschreibung="ziel.zielbeschreibung"
                 :wartende="ziel.wartende" :dauer="ziel.geplanteraufenthaltinMinuten"
                 :geplanteankunft="ziel.geplanteankunft" :googlemapsurl="ziel.googlemapsurl">
    </Etappenziel>
  </v-container>
</template>

<script>
    import Etappenziel from "./Etappenziel";
    import axios from 'axios';

    export default {
        name: "Status.vue",
        data: function () {
            return {
                etappenziele: []
            }
        },
        created() {
            axios.get('/etappen/')
                .then(res => {
                    this.etappenziele = res.data;
                })
                .catch(err => console.log(err));
        },
        components: {
            Etappenziel
        },

    }

</script>

