import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Status from '../components/Status'
import Rallye from "../components/Rallye";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
        meta: {
            title: "Hochzeitsrallye Home"
        },
    },
    {
        path: '/rallye',
        name: 'Rallye',
        component: Rallye,
        meta: {
            title: "Hochzeitsrallye"
        },
    },
    {
        path: '/status',
        name: 'Status',
        component: Status,
        meta: {
            title: "Hochzeitsrallye Etappenübersicht"
        },
    }
]

const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    document.title = to.meta.title || 'Hochzeitsrallye';
    next();
})

export default router
