<template>
  <v-container>
    <v-card class="mx-auto" max-width="400">
      <v-card-title class="headline">{{zielbeschreibung}}</v-card-title>
      <v-card-subtitle class="overline">Etappenziel Nr. {{nr}}</v-card-subtitle>
      <v-card-text>
        <p>{{wartende}}</p>
        <p>Geplante Ankunft: {{uhrzeit}} Uhr</p>
        <p>Geplanter Aufenthalt: {{dauer}} Minuten</p>
        <v-alert dense v-if="!zielerreicht"
                 color="red lighten-2"
                 dark
        >Etappe noch nicht abgeschlossen
        </v-alert>
        <v-alert dense v-if="zielerreicht"
                 color="green lighten-2"
                 dark
        >Etappe abgeschlossen
        </v-alert>
      </v-card-text>
      <v-card-actions>
        <v-btn :href="googlemapsurl" text>
          <v-icon left>mdi-map</v-icon>
          Karte anzeigen
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
    import moment from 'moment'

    export default {
        name: "Etappenziel",
        props: {
            nr: Number,
            zielbeschreibung: String,
            wartende: String,
            googlemapsurl: String,
            codename: String,
            zielerreicht: Boolean,
            geplanteankunft: Date,
            dauer: Number,
        },
        methods: {
            format_date(value) {
                if (value) {
                    return moment(String(value), moment.defaultFormatUtc).format('hh:mm')
                }
            },
        },
        computed: {
            uhrzeit: function () {
                return moment(String(this.geplanteankunft), moment.defaultFormatUtc).format('hh:mm')
            }
        },
    }
</script>

