<template>
  <v-container>
    <div v-if="aktuelleEtappe != null">
      <h1 class="my-2 d-flex justify-center">Aktuelles Etappenziel</h1>
      <v-card class="mx-auto" max-width="500">
        <v-card-title class="headline">{{aktuelleEtappe.zielbeschreibung}}</v-card-title>
        <v-card-subtitle class="overline">Etappenziel Nr. {{aktuelleEtappe.nr}}</v-card-subtitle>
        <v-card-text>
          <p>Geplante Ankunft: {{this.uhrzeit}}</p>
          <p>Geplanter Aufenthalt: {{aktuelleEtappe.geplanteraufenthaltinMinuten}} Minuten</p>
        </v-card-text>
        <v-card-actions>
          <v-btn :href="aktuelleEtappe.googlemapsurl">
            <v-icon left>mdi-map</v-icon>
            Karte anzeigen
          </v-btn>
        </v-card-actions>
      </v-card>
      <v-btn class="mt-8 d-flex" @click="beendeEtappe">Weiter zur nächsten Etappe</v-btn>
    </div>


    <div v-if="aktuelleEtappe == null">
      <h1 class="my-5 d-flex justify-center">Ziel erreicht!</h1>
      <p class=" my-5 text-justify">Wir wünschen, dass euch dies immer gemeinsam durchs Leben führen wird:</p>
      <v-list>
        <v-list-item v-for="ziel in etappenziele" :key="ziel.id">
          <v-list-item-content> {{ ziel.codename}}</v-list-item-content>
        </v-list-item>
      </v-list>
    </div>

  </v-container>
</template>

<script>
    import axios from "axios";
    import moment from "moment";

    export default {
        name: "Rallye",
        data: function () {
            return {
                etappenziele: []
            }
        },
        methods: {
            beendeEtappe: function () {
                for (let i = 0; i < this.etappenziele.length; i++) {
                     if (this.etappenziele[i].id == this.aktuelleEtappe.id) {
                         this.etappenziele[i].zielerreicht = true;
                         return;
                     }
                }
            }
        },
        created() {
            axios.get('/etappen/')
                .then(res => {
                    console.log(res.data);
                    this.etappenziele = res.data;
                    this.etappenziele.sort(function (a, b) {
                        return a.nr - b.nr;
                    });
                })
                .catch(err => console.log(err));
        },
        computed: {
            aktuelleEtappe: function () {
                console.log("computed")
                for (let i = 0; i < this.etappenziele.length; i++) {
                    console.log(this.etappenziele[i].nr);
                    if (!this.etappenziele[i].zielerreicht)
                        return this.etappenziele[i];
                }
                return null
            },
            uhrzeit: function () {
                if (this.aktuelleEtappe!=null) {
                    return moment(String(this.aktuelleEtappe.geplanteankunft), moment.defaultFormatUtc).format('hh:mm')
                }
                else {
                    return null
                }
            }
        }

    }
</script>

<style scoped>

</style>
